<template>
    <div>
        <el-card shadow='never'>
            <div slot="header" class="clearfix">
                <span>售后订单列表</span>

            </div>
            <div class="search_fun">
                 <el-select v-model="key" class='small' style="width:100px" size='small' placeholder="请选择">
                    <el-option v-for="(item,index) in list" :key="index" :label="item" :value="index">
                    </el-option>
                </el-select>
                <el-input v-model="keyword" class='search_input' size='small' placeholder="用户账号、订单id、产品名称"></el-input>
                <el-button type="primary" size='small' @click='search'>搜索</el-button>
            </div>
            <el-table :data="tableData" stripe style="width: 100%;height:100%">
                <el-table-column prop="Id" label="Id" width="60">
                </el-table-column>
                <el-table-column prop="OrderId" label="OrderId">
                    <template slot-scope="scope">
                        <router-link class='color-a' :to="'/ordermanage/order_detail?OrderId='+scope.row.Id">
                        {{scope.row.OrderId}}
                        </router-link>
                    </template>
                </el-table-column>
                <el-table-column prop="Account" label="用户名">
                </el-table-column>
                <el-table-column prop="ProductName" label="产品名">
                    
                 
                </el-table-column>
                <el-table-column prop="Amount" label="数量">
                   
                </el-table-column>
                <el-table-column prop="EndTime" label="创建时间">
                    <template slot-scope="scope">
                        {{scope.row.CreateTime | date}}
                    </template>
                </el-table-column>
                <el-table-column prop="EndTime" label="过期时间">
                    <template slot-scope="scope">
                        <span v-if="scope.row.ExpireTime">{{scope.row.ExpireTime | date}}</span>
                        <span v-else></span>
                    </template>
                </el-table-column>
                <el-table-column prop="PresellPayment" label="预售支付方式">
                    
                </el-table-column>
                <el-table-column prop="Payment" label="支付方式">
                   
                </el-table-column>
                <el-table-column prop="Discount" label="已支付BTC">
                   
                </el-table-column>
                <el-table-column prop="PresellShouldPayBtcPrice" label="预付需支付BTC">
                   
                </el-table-column>
                <el-table-column prop="TotalShouldPayBtcPrice" label="总需支付BTC">
                   
                </el-table-column>
                <el-table-column prop="TotalShouldPayUsdPrice" label="总需支付美元">
                   
                </el-table-column>
                <el-table-column prop="TotalShouldPayUsdPrice" label="操作">
                    <template slot-scope="scope">
                        <el-button size="mini" @click='show(scope.row)' type='text'>修改价格</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination class='page' :current-page.sync="pageNo" @current-change='getList' background layout="prev, pager, next" :total="totalNo">
            </el-pagination>
        </el-card>
         <el-dialog
                title="修改价格"
                :visible.sync="dialogVisible"
                width="30%">
            <h4>ID</h4>
            <el-input
                    :placeholder="this.temporder.Id"
                    :disabled="true">
            </el-input>
            <h4>订单ID</h4>
            <el-input
                    :disabled="true"
                    :placeholder="this.temporder.OrderId">
            </el-input>
            <h4>人民币单价</h4>
            <el-input
                    placeholder="人民币单价"
                    v-model="input2"
                    clearable>
            </el-input>
            <h4>美元单价</h4>
            <el-input
                    placeholder="美元单价"
                    v-model="input3"
                    clearable>
            </el-input>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="submitPrice">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    name: "Home",
    data() {
        return {
            dialogVisible:false,
            temporder:{},
            tableData:[],
            list:{
                "orderNo":"订单ID",
                "account":"用户账号",
                "productName":"产品名",
            },
            totalNo: 0,
            key:'orderNo',
            keyword:'',
            pageNo:1,
            input2:'', // 人民币单价
            input3:''  // 美元单价
        };
    },
    computed: {

    },
    methods: {
        show(order){
            this.input2 = order.ProductOriginPrice
            this.input3 = order.ProductOriginUsdPrice
            this.dialogVisible = true;
            this.temporder = order;
        },
        getList() {
            let params = {
                pageNo: this.pageNo - 0,
                pageSize: 10
            };
            params[this.key] = this.keyword;
            this.$api("/admin/after/get_after_buy_order_list", params).then(res => {
                this.tableData = res.data.orderList;
                this.totalNo = res.data.page.totalNum;
            });
        },
        submitPrice(){
                if(this.input2 && this.input3){
                    this.$api('/admin/order/update_order_price', {
                        orderId: this.temporder.Id,
                        productOriginPrice: this.input2,
                        productOriginUsdPrice: this.input3
                    }).then(res => {
                        // console.log(res)
                        this.$message({
                        type: 'success',
                        message: '操作成功!'
                    });
                    this.dialogVisible = false
                    // 刷新页面，获取修改数据，有瞬间空白页待优化
                    this.getList();
                });
                }
            },
        search() {
            this.pageNo = 1;
            this.getList();
        },
    },
    created() {
        this.getList();
        // this.getFacilityDetail(16)
    }
};
</script>

<style lang='scss' scoped>
.search_fun {
    height: 60px;
    position: relative;
    top: 10px;
    float: right;
}
.search_input {
    width: 200px;
}
</style>



